.f2 
{
    width: 100%;
    height: 3589px;
    background:black;
    margin:0 auto;
    overflow:hidden;
    position:relative;
    margin-bottom:33px;
    display:flex;
    justify-content:center;
    margin-top:30px;
}
.bgtext 
{
    color: rgba(255, 255, 255, 0.04);
    font-family: Outfit;
    font-size: 128px;
    font-style: normal;
    font-weight: 900;
    line-height: normal;
    margin-top:61px;
    width:2000px;
    margin-left:-2950px;
    position:absolute;
}
.bgtext-2
{
    color: rgba(255, 255, 255, 0.04);
    font-family: Outfit;
    font-size: 128px;
    font-style: normal;
