    line-height: normal;
    width:1883px;
    bottom:300px;
    position:absolute;

}
.svg
{
  margin-top:400px;
  margin-left:-900px;
  backdrop-filter:blur(10px);
}
.explore 
{
    display: flex;
    max-width: 492px;
    max-height:60px;
    padding: 16px 24px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border-radius: 64px;
    background: rgba(255, 255, 255, 0.04);
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.25);
    backdrop-filter: blur(40px);
    color: #FFF;
    font-family: Outfit;
    font-size: 48px;
    font-style: normal;
    font-weight: 500;
    margin-top:130px;
}
@media (max-width: 600px) 
{
    .f2{
        justify-content:center;
    }
    .explore{
        width:300px;
        font-size:35px;
    }
    .import{
      width:300px;
      height:600px; 
      margin-top:-60px;
    } 
    .image{
       margin-left:-60px;
    }
    .frame20{
        width:300px;
        height:600px; 
        display:flex;
        flex-direction:row;
        margin-top:0px;
    }
    .roundedcorner{
        width:300px;
        height:30px;
        left:0px;
    }
    .frame17{
        display:flex;
        flex-direction:row;
        justify-content:center;

        width:300px;
        height:600px;
    }
    .frame16-reverse{
        width:400px;
        height:600px;
    }
    .frame16-reverse .frame12{
        margin-left:1000px;
        margin-top:-150px;
    }
    .frame16-reverse .frame15{
        margin-right:0px;
        width:400px;
        height:300px;
        margin-top:500px;
    }
    .video-content3{
        width:300px;
        height:600px; 
        display:flex;
        flex-direction:row;
        margin-top:0px;
    }
    .baseline{
        width:300px;
        height:30px;
        margin-left:0px;
        margin-top:20px;
    }
    .video-content4{
        margin-top:0px;
    }
    .frame19{
        margin-top:0px;
    }
    .bgtext-2{
        margin-bottom:-120px;
    }
}

