.Fill-form 
{
    width:638px;
    height:425px;
    position:absolute;
    top:60px;
    left:672px;
}
.details
{
    display: flex;
    width: 578px;
    padding: 27px 30px;
    align-items: center;
    gap: 10px;
    border-radius: 53px;
    border: 1px solid #000;
    background: #FFF;
    margin-bottom:27px;
}
.send
{
    display: inline-flex;
    padding: 16px 24px;
    align-items: center;
    gap: 16px;
    border-radius: 43.707px;
    background: #000;
    backdrop-filter: blur(1.821117639541626px);
    color: #FFF;
    font-family: Outfit;
    font-size: 21.853px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    right:0px;
    position:absolute;
    cursor:pointer;
}
@media (max-width:600px){
    .Fill-form{
        width:240px;
        margin-left:-640px;
        margin-top:100px;
    }
    .details{
        width:200px;
        height:20px;
        margin-bottom:10px;
    }
}