.frame16 
{
   display: inline-flex;
   align-items: center;
   gap: 86px;
   max-width:1280px;
   max-height:438px;
   margin-bottom:48px;
}
.frame16-reverse 
{
   display: inline-flex;
   flex-direction:row-reverse;
   align-items: center;
   gap: 86px;
   max-width:1268px;
   max-height:438px;
   margin-bottom:48px;
}
.frame12 
{
    position:relative;
    left:-300px;
}
.Rectangle 
{
    position:relative;
    max-height:430px;
    max-width:500px;
}
.Arrow 
{
    position:absolute;
    top:275px;
    left:360px;
    max-width:100px;
    max-height:100px;
}
.Ellipse 
{
    position:absolute;
    max-width: 167.625px;
    max-height: 167.625px;
    top:239.63px;
    left:325.12px;
    backdrop-filter:blur(10px);
    border-radius:84px;
}
.frame15
{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top:-100px;
    gap: 24px;
    margin-left:0px;
    max-width:677px;
    max-height:264px;
    color:white;
}
.header-content 
{
    color: #FFF;
    font-family: Outfit;
    font-size: 48px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    margin-bottom:0px;
}
.desc-content 
{
    color: #FFF;
    text-align: justify;
    font-family: Outfit;
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-top:0px;
}
@media (max-width: 600px){
    .frame16{
    display:flex;
    flex-direction:column;
    height:600px;
    }
    .header-content {
        font-size:30px;
    }
    .desc-content {
        font-size:15px;
    }
    .frame12{
        margin-left:0px;
    }
    .Ellipse{
        width:120px;
        top:170px;
        left:170px;
    }
    .Rectangle{
        height:300px;
        width:300px;
    }
    .Arrow{
        height:80px;
        top:190px;
        left:190px; 
    }
    .frame15{
        width:300px;
        height:300px;
    }
}