.framehead 
{
   display: inline-flex;
   align-items: center;
   gap: 86px;
   max-width:1280px;
   max-height:438px;
   margin-bottom:48px;
}
.framex2{
    position:relative;
    left:-300px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top:-50px;
    gap: 24px;
    margin-left:0px;
    max-width:677px;
    max-height:264px;
    color:white;
}
.framex1 
{
    position:relative;
}
.Rectangle2 
{
    position:relative;
    max-height:430px;
    max-width:500px;
}
.Arrow2
{
    position:absolute;
    top:280px;
    right:40px;
    max-width:100px;
    max-height:100px;
}
.Ellipse2
{
    position:absolute;
    max-width: 167.625px;
    max-height: 167.625px;
    top:240px;
    right:10px;
    backdrop-filter:blur(10px);
    border-radius:84px;
}
@media (max-width: 600px){
    .framehead{
    display:flex;
    flex-direction:column;
    height:600px;
    gap:20px;
    }
    .header-content {
        font-size:30px;
    }
    .desc-content {
        font-size:15px;
    }
    .framex1{
        margin-left:0px;
        margin-top:-570px;
    }
    .Ellipse2{
        width:120px;
        top:170px;
        left:170px;
    }
    .Rectangle2{
        height:300px;
        width:300px;
    }
    .Arrow2{
        height:80px;
        top:190px;
        left:190px; 
    }
    .framex2{
        width:300px;
        height:300px;
        margin-top:270px;
    }
}