.navbar {
    position:fixed;
    display:flex;
    justify-content:center;
    align-items:center;
    top:-200px;
    left:10%;
    height:48px;
    width:1280px;
    border-radius:48px;
    gap:540px;
    padding: 14px 24px 14px 36px;
    border: 1px solid rgba(87, 87, 87, 0.32);
    background: rgba(255, 255, 255, 0.23);
    backdrop-filter: blur(39.5px);
    font-family:outfit;
}
.menu-icon{
    display:none;
}
@media (max-width:600px){
    .navbar 
    {
        height:30px;
        width:260px;
        top:-220px;
        left:8%;
    }
    .menu-icon{
        display:block;
    }
    .connect{
        display:none;
    }
    .menu-connect{
        color:white;
        margin-top:20px;
    }
    .menu-btn{
        width:300px;
        padding:35px;
        margin-bottom:20px;
        font-size:20px;
        background:black;
        color:white;
        align-items:center;
        display: flex;
        justify-content: center;
       align-items: center;
       gap: 10px;
      cursor:pointer;
    }
    .navbar-links-container a{
        display:none;
    }
}
.flex
{
    color:black;
}
.active 
{
    color:white;
}
.active-link 
{
    color:white;
}
.flex-link
{
    color:black;
}
.scrolled
{
    color:white;
}
.btn 
{
    color:white;
    border-radius: 48px;
    background: #1F1F1F;
    backdrop-filter: blur(2px);
    padding: 12px 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    cursor:pointer;
    margin-left:300px;
}
.menu-links{
    position:absolute;
    list-style:none;
    display:flex;
    flex-direction:column;
    align-items:center;
    width:100%;
    top:40px;
    gap:80px;
    font-size:30px;
    background: white;
    height:400px;
    border: 1px solid rgba(87, 87, 87, 0.32);
}
.menu-links li {
}
.navbar-links-container 
{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 64px;  
    font-family:outfit;
    margin-right:-450px;
}
.navbar-menu-items{
    position:absolute;
    display:flex;
    flex-direction:column;
    width:100%;
    top:80px;
    left:0px;
    justify-content:space-between;
    align-items:center;
    background:rgba(87, 87, 87, 0.32);
}
.navbar-menu-items a {
    height:100px;
    font-size:20px;
    margin-top:20px;
    background:black;
    width:300px;
    color:white;
    display:flex;
    align-items:center;
    justify-content:center;
}
.logo-container
{
    height:24px;
    position:absolute;
    left:30px;
}
.logo 
{
    cursor:pointer;
    width:125px;
}
.menu-icon
{
    right:20px;
    position:absolute;
    color:black;
}
.nav-links 
{
    color:black;
}
